<template>
	<view class="side-box">
		
		<!-- 论坛内容列表 -->

		
		<view class="" style="padding-bottom: 100rpx;">
			<view style="margin-bottom: 50rpx;" v-for="(item,index) in articleList" :key="index" @click="toArticleDetail(item.id)">
				<view style="display: flex;align-items: center;">
					<image :src="item.avatar && item.avatar.split(',')[item.avatar.split(',').length-1]" mode="" style="width: 90rpx;height: 90rpx;border-radius: 100%;margin-right: 20rpx;" @click="toTalkPage(item.userName,item.createdBy)"></image>
					<view>
						<view style="color: #333;font-weight: 550;">{{item.userName}}</view>
						
						<view style="margin-top: 10rpx;font-size: 22rpx;color: #666;">
							<!-- <text style="margin-right: 20rpx;">已关注</text> -->
							<text>{{happenTimeFun(item.createdAt)}}</text>
						</view>
					</view>
					<!-- <view class="" style="margin-left: 200rpx;color: #000;">关注</view> -->
				</view>
				
				<view style="color: #000;margin: 20rpx 0;">{{item.content}}</view>
				<view class="">
					<image :src="item.imgUrl" mode="aspectFill" style="width: 430rpx;height: 600rpx;border-radius: 5rpx;" v-if="item.imgUrl !== ''"></image>
					<view class="" v-else></view>
				</view>
				
				<view style="margin-top: 20rpx;display: flex;align-items: center;" v-if="item.momentCommentList.length > 0">
					<view class="" style="color:orangered;font-weight: 700;display: flex;margin-right: 20rpx;">
						<image src="/static/icons/hot-icon.png" mode="" style="width: 40rpx;height: 40rpx;margin-right: 10rpx;"></image> 热评
					</view>
					<view class="" style="" >{{item.momentCommentList[0].comment}}</view>	
				</view>
				
				<view style="display: flex;align-items: center;margin-top: 36rpx;">
				
					
					<view style="display: flex;align-items: center;margin-right: 80rpx;">
						<image src="/static/icons/评论.png" mode="" style="width: 50rpx;height: 50rpx;margin-right: 15rpx;"></image>
						<text style="color: #666;font-weight: 550;">{{item.momentCommentList.length}}</text>
					</view>	
					
					
				</view>		
			</view>
		</view>
		
		<!-- 底部 -->
		<view class="footer-buttons">
			<view>
				<image src="/static/icons/tuijian-yes.png" mode="" style="width: 40rpx;height: 40rpx;"></image>
				<view class="" style="font-size: 22rpx;color: #666;">推荐</view>
			</view>	
			
			<view class="" @click="add_Q">
				<image src="/static/icons/add-icon.png" mode="" style="width: 30rpx;height: 30rpx;"></image>
			</view>
			
			<view class="" @click="toHelpPage">
				<image src="/static/icons/help-no.png" mode="" style="width: 50rpx;height: 50rpx;"></image>
				<view class="" style="font-size: 22rpx;color: #666;">求助区</view>
			</view>
		</view>
		
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				articleList:[]
			}
		},
		onLoad() {
			this.getTypeList()
		},
		onShow() {
			this.getTypeList()
		},
		methods: {
			toArticleDetail(id){
				uni.navigateTo({
					url:'../article-detail/article-detail?id='+id
				})
			},
			toHelpPage(){
				uni.navigateTo({
					url:'../help-page/help-page'
				})
			},
			add_Q(){
				uni.navigateTo({
					url:'../add-article/add-article'
				})
			},
			getTypeList(){
				uni.request({
					url:this.$elyasApi+'article/findByModal',
					method:'POST',
					data:this.selectItems,
					header:{
						"content-type":"application/json",
						"accessToken":this.$store.state.userInfo.token
					},
					success:(data)=> {
						this.articleList = data.data.data.list;
					}
					
				});
			},
			happenTimeFun(num){//时间戳数据处理
						
			let date = new Date(num);
			
			//时间戳为10位需*1000，时间戳为13位的话不需乘1000
			
			        let y = date.getFullYear();
			
			        let MM = date.getMonth() + 1;
			
			        MM = MM < 10 ? ('0' + MM) : MM;//月补0
			
			        let d = date.getDate();
			
			        d = d < 10 ? ('0' + d) : d;//天补0
			
			      
			        return y + '-' + MM + '-' + d;
			
			},
			toTalkPage(name,id){
				uni.navigateTo({
					url:'../chat-window/chat-window?name='+name+'&id='+id
				})
			}
		}
	}
</script>

<style>
	.side-box{
		padding: 30rpx;
	}
	
	.footer-buttons{
		position: fixed;
		bottom: 0rpx;
		left: 0;
		height: 110rpx;
		width: 750rpx;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 50rpx;
		background-color: #fff;
	}
</style>
